import uniIcon from '@/components/uni-icons/uni-icons.vue'
import linkUrl from "@/components/link/link.vue"
import banner from "@/components/banner/banner.vue"
import saveimg from "@/components/saveimg/saveimg.vue"
export default {
	components:{
			uniIcon,linkUrl,banner,saveimg
	},
	data() {
		return {
			collectionI:false,
			title: 'Hello',
			companyInformation:null,
			qrcode:'',
			collectionT:'收藏'
		}
	},
	onLoad(option) {
		if(option.qcode){
			this.qrcode = option.qcode;
		}
		
		if(option.scene){
			this.qrcode = decodeURIComponent(option.scene)
		}
		this.getQcode();
	},
	methods: {
		
		phone(){
			let phone = this.companyInformation.realPhone
			uni.makePhoneCall({
				phoneNumber:phone,
				success:function(){
					uni.showToast({
						"title":"正在拨打电话",
						"icon":"none"
					})
				}
			})
		},
		link(){
			
		},
		Nav(){
			uni.openLocation({
			    latitude: parseFloat(this.companyInformation.longitude),
			    longitude: parseFloat(this.companyInformation.latitude),
			    success: function () {
			        console.log('success');
			    }
			});
		},
		toTab(){
			console.log(1);
			uni.showModal({
				"title":"提示",
				"content":"即将跳转至二维码申请页，请确定",
				"success":function(res){
					if(res.confirm){
						console.log(res)
						uni.reLaunch({
							"url":"/pages/address/msdh/index"
						})
					}
				}
			})
			
		},
		toUrl(url){
			uni.navigateTo({
				"url":url
			})
		},
		getQcode(){
			let _this = this;
			
			this.$ajax("/nav/detail","POST",{qrcodeNo:_this.qrcode}).then((res)=>{
				
				_this.companyInformation = res.detail;
			})
			
			
		},
		preview(i,type){
			let _this = this;
			var url = "";
			if(type == 0){
				
				url = _this.companyInformation.realImgs
			}else{
				url = _this.companyInformation.innerImgs
			}
			uni.previewImage({
				urls: url
			});
		},
		collection(){
			if(this.collectionI){
				this.collectionI = !this.collectionI;
				this.collectionT = '收藏'
				uni.showToast({
					"title":"收藏成功"
				})
			}else{
				this.collectionI = !this.collectionI;
				
				this.collectionT = '取消'
				uni.showToast({
					"title":"取消收藏成功"
				})
			}
			
			
		}
		
	}
}
